.root {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
  color: #ffffff;
  letter-spacing: 1px;

  height: 52px;

  background: linear-gradient(276.53deg, #2e2eed -93.34%, #4e4ef9 82.6%);
  box-shadow: inset 0px -2px 0px rgba(0, 0, 0, 0.135107);
  border-radius: 25px;
  padding-left: 25px;
  padding-right: 25px;
  min-width: 200px;
}
.label {
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
