@import "sass/index";
$blockBorderRadius: 10px;

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
}

.gameBlock {
  overflow: hidden;
  border-radius: $blockBorderRadius;
  width: 100%;
  height: 100%;
}

.statisticsWrapper {
  padding-top: 26px;
}

.gameCell {
  height: 100%;
  min-height: 587px;
  display: grid;
  grid-template-columns: 6.3fr 3.7fr;
  @media (max-width: 1280px) {
    grid-template-columns: 1fr;
  }
  @media (max-width: $mobileScreen) {
    min-height: 375px;
  }
}
.gameWrapper {
  width: 100%;
  height: 100%;
  position: relative;
  background: #{use(--template-gameBlock-background)} url(/assets/logos/background-pasino-logo.svg)
    no-repeat;
  background-blend-mode: difference;
  background-position: center;
  background-size: 99%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profitTable {
  background: #{use(--template-gameCell-background)};
  @media (max-width: 1280px) {
    display: none;
  }
}
.profitTableMobile {
  width: 100%;
  margin-top: 15px;
  max-height: 614px;
  height: 100%;
  background: #{use(--template-gameCell-background)};
  border-radius: $blockBorderRadius;
  padding-top: 10px;
  display: flex;
}
.gameControlBlock {
  margin-top: 15px;
  padding: 30px 62px 30px 72px;
  background: #{use(--template-gameBlock-background)};
  border-radius: $blockBorderRadius;
  @media (max-width: 1200px) {
    padding: 15px 20px;
  }
  @media (max-width: 900px) {
    padding: 20px 10px;
  }
}
