@import "sass/index";
.selectionButton {
  width: 100%;
  height: 60px;
  color: #313149;
  border-radius: 8px;
  display: inline-block;
  background-size: 0 0;
  position: relative;
  z-index: 0;
  background-image: var(--selectionButtonBackground);

  button {
    width: 100%;
    height: 100%;
    padding: 17px 0 16px;
    text-align: center;
    border: none;
    font-family: "Gilroy";
    font-style: normal;
    font-weight: 600;
    font-size: 23px;
    line-height: 27px;
    background: none;
    cursor: pointer;
    color: #{use(--primary-contrastText)};
  }

  &.active {
    &::before {
      display: none;
    }
    button {
      color: #ffffff;
    }

    background-size: 100%;
  }
}

.selectionButton::before {
  border-radius: 8px;
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: inherit;
  background-size: auto;
  -webkit-mask: url('data:image/svg+xml;utf8,<svg  xmlns="http://www.w3.org/2000/svg" ><rect x="1.5" y="1.5" width="100%" height="100%" style="height:calc(100% - 3px);width:calc(100% - 3px)" rx="8" ry="8" stroke-width="3" fill="transparent" stroke="white"/></svg>');
  mask: url('data:image/svg+xml;utf8,<svg  xmlns="http://www.w3.org/2000/svg" ><rect x="1.5" y="1.5" width="100%" height="100%" style="height:calc(100% - 3px);width:calc(100% - 3px)" rx="8" ry="8" stroke-width="3" fill="transparent" stroke="white"/></svg>');
}
