@import "sass/index";

$buttonBorderRadius: 6px;

.button {
  height: 100%;
  font-family: "Gilroy";
  color: rgba($white, 0.5);
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  padding-right: 0;
  padding-left: 0;
  min-width: 47px;
  padding: 13.5px 0 12.5px;
}

.btnLeft {
  border-radius: $buttonBorderRadius;
}

.btnRight {
  border-radius: $buttonBorderRadius;
}

.btnCenter {
  border-radius: $buttonBorderRadius;
}

.mobileInputBtn {
  position: relative;
  width: 100%;
}
