@import "sass/index";
.root {
  z-index: 5;
  position: absolute;

  width: 280px;
  height: 151px;
  user-select: none;
  top: calc(50% + 3px);
  left: 50%;
  transform: translate(-50%, -50%);
  animation: emergence 1s;
}

.wrapper {
  border: 5px solid #00ffd7;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.7);
  height: 100%;
  width: 100%;
  padding: 24px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: space-between;
}
.lineElement {
  width: 63.82px;
  height: 5px;
  background: #ffffff;
  opacity: 0.2;
  border-radius: 2px;
}

.ratio {
  margin: 0;
  line-height: 34px;
  text-align: center;
  color: #00ffd7;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 28px;
}
.timer {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 28px;
  line-height: 34px;

  color: #ffffff;

  h5 {
    margin: 0;
    padding: 0;
  }
}

.amount {
  text-align: center;
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 20px;
  &.textProfit {
    color: use(--template-liveStats-bets-section-win);
  }
  &.textFail {
    color: use(--subsidiary-red);
  }
}
.stoppedResult {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 38px;

  text-transform: capitalize;

  color: #ffffff;

  h5 {
    margin: 0;
    padding: 0;
  }
}
.coin {
  position: relative;
  left: 5px;
  top: 5px;
  width: 26px;
  height: 26px;
}
.betMessage {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;

  text-transform: capitalize;

  color: #ffffff;

  h5 {
    margin: 0;
    padding: 0;
  }
}

@keyframes emergence {
  from {
    transform: scale(0) translate(-50%, -50%);
  }
  to {
    transform: scale(1) translate(-50%, -50%);
  }
}
