@import "../../variables.scss";

.root {
  z-index: 10;
  position: absolute;

  width: $prizeCardWidth;
  height: $prizeCardHeight;
  user-select: none;
  top: calc(50% - (#{$prizeCardHeight}/ 2));
  transform: translate(40%, -50%);
  right: 0;
  animation: opacity 0.3s;
}

.wrapper {
  background-image: url(/assets/fortune/treasure_bg.png);
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;
  width: 100%;
  position: relative;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
}
.chest {
  width: 73.8px;
  height: 72.05px;
}
.chest_selection {
  position: absolute;
  bottom: 15px;
  right: 9px;
  font-family: "Gilroy";
  font-weight: 900;
  font-size: 14.7526px;
  line-height: 19px;
  color: #15fce0;
}

@keyframes opacity {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
