@import "sass/index";
@import "config/themes/_fortune";

.switchWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  & > span:first-child {
    margin-right: 10px;
  }
  & > span:last-child {
    margin-left: 10px;
  }
}

.option {
  text-transform: uppercase;
  text-align: center;
  color: use(--template-gameCell-switch-text);
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;

  letter-spacing: 1px;
  cursor: pointer;
  user-select: none;
}

.highlight {
  color: use(--template-gameCell-switch-selectedText);
}

.switchToggle {
  width: 16px;
  height: 16px;
  top: 2px;
  color: white;
  box-shadow: 0px 1px 2px rgba(145, 158, 171, 0.24);
}

.switchTrack {
  height: 13px;
}
