@import "sass/index";
@import "config/themes/_fortune.scss";

.input {
  border: 1px #2e2eed solid;
  position: relative;
  overflow: hidden;
  background: use(--template-anyContent-background);
  height: 60px;
}

.button {
  background: $elephant;
}

.refreshIcon {
  & > path {
    fill: $elephant;
  }
}
