@import "sass/index";
@import "./variables.scss";

.box {
  margin: 0;
  padding: 20px;
  overflow: hidden;
  position: relative;
  height: 100%;
  width: 100%;
  p {
    margin: 0;
    padding: 0;
  }
}
.shadow {
  position: absolute;
  box-shadow: var(--template-gameCell-shadow);
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 1px;
}
.table {
  width: 100%;
}

.userAmountWrapper {
  position: relative;
  top: -0.34rem;

  overflow: visible;
}
.gridTemplateColumns {
  grid-template-columns: $tableGridTemplateColumns;
}
.playerText {
  p {
    line-height: 12px;
  }
}
.textCoin {
  padding-left: 5px;
  margin: 0;
}
.textFail {
  color: use(--subsidiary-red) !important;
}
.textProfit {
  color: use(--subsidiary-blue) !important;
}
.btcIcon {
  position: static;
  height: 15.23px;
  width: 15.23px;
  cursor: default;
  color: black;
}
.userName {
  font-weight: bold;
}
.tableWrapper {
  overflow: hidden;
  height: 100%;
  overflow-y: auto;
  max-height: 500px;
}
