.controlPanelFooter {
  width: 100%;
  height: 100%;
}
.autoMode {
  display: grid;
  grid-template-areas:
    "onWin onLoss"
    "stopOnProfit stopOnLoss";
  gap: 20px;
  @media (max-width: 1230px) {
    grid-template-areas:
      "numberOfBets" "onWin" "onLoss"
      "stopOnProfit" "stopOnLoss";
    grid-template-columns: 1fr;
  }
}

.onWin {
  grid-area: onWin;
}

.onLoss {
  grid-area: onLoss;
}
.numberOfBets {
  grid-area: numberOfBets;
}

.stopOnProfit {
  grid-area: stopOnProfit;
  label:nth-of-type(1) {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }
}

.stopOnLoss {
  grid-area: stopOnLoss;
  label:nth-of-type(1) {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }
}
.buttons {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
  grid-gap: 5px;
  margin-top: 15px;
  @media (max-width: 600px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }
}
.betButton {
  @media (max-width: 600px) {
    order: -1;
  }
}
.buttonText {
  margin: 0;
  line-height: 100%;
}
.helpButtonText {
  font-size: 0.7rem;
  line-height: 14px;
  display: block;
  margin: 0;
}
.optionsButtons {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  > div {
    justify-content: center;
    margin: 0;
    align-items: center;
    button {
      width: 35px;
      height: 35px;
      margin-right: 7px;
    }
  }
}
