@import "sass/index";
.betBody {
  display: grid;
  grid-auto-rows: minmax(60px, auto);
  grid-template-areas:
    "selectCoin amount"
    "selections selections";
  gap: 20px;
  margin-bottom: 15px;
  @media (max-width: 1248px) {
    grid-template-areas:
      "selectCoin " "amount"
      "selections";
  }
}
.inputWrapper {
  position: relative;
}
.dollarLabel {
  position: absolute;
  left: 9px;
  top: 15%;
  height: 100%;
  font-size: 16px;
  color: white;
  display: flex;
  align-items: center;
  &.disabled {
    filter: brightness(0.6);
  }
}
.selectedAmount {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 400;
  font-size: 23px;
  line-height: 27px;
  color: #{use(--template-statisticsBlock-tab-color)};
  mix-blend-mode: normal;
}
.btnWrapper {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 3px;
}
.selectCoin {
  grid-area: selectCoin;
}
.select {
  height: 100%;
  box-shadow: 0px 2px 3px -3px;
}
.amount {
  grid-area: amount;
}
.selections {
  grid-area: selections;
  display: grid;
  width: 100%;
  grid-gap: 11.3px;
  gap: 11.3px;
  grid-template-columns: repeat(7, 1fr);

  @media (max-width: 600px) {
    grid-template-columns: 1fr 1fr;
    grid-gap: 14px;
    gap: 14px;
  }
}
.label {
  color: #{use(--template-statisticsBlock-tab-color)};
}
